.App {
  text-align: center;
  width: 100%;
  height: 100vh;
}

.pokemonCardContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 20px;
  margin: 20px auto 0;
  justify-items: center;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: start;
  margin: 16px 0 0;
}
